  middleware: "authentication",
  mounted: async function() {
      this.loading = true;
      let holders = await this.getHolders();
      let holderList = [];

        let fatcakePrice = await this.getFatCakePrice();
        let cakePrice = await this.getCakePrice();

        for (let i = 0; i < holders.length; i++) {
            let rewards = await this.getAllRewards(holders[i].address);

            let address = holders[i].address;
            let decimals = holders[i].contract_decimals;
            let balanceWithDecimals = holders[i].balance;
            let balance = balanceWithDecimals / Math.pow(10, decimals);

            holderList.push({
                id: i + 1,
                address: address,
                balance: balance.toFixed(0).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                balanceUSD: (balance * fatcakePrice).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
                rewards: rewards.toFixed(5),
                CAKErewards: (rewards * cakePrice).toFixed(2).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ","),
            });
        }

      this.userList = holderList;
      this.loading = false;
  },
  methods: {
      getHolders: async function() {
          try {
              let result = await axios.get("https://api.covalenthq.com/v1/56/tokens/0xB7Dba4C673beDB174DC3Ff7Ec65d17C863d39b16/token_holders/?&key=ckey_74d1bbf78ce8435a9055327cf20");

            let holders = result.data.data.items;
            let top5Holders = [];

            let index = 0;
            while(top5Holders.length != 10) {
                if (!this.blacklistAddresses.includes(holders[index].address)) {
                    top5Holders.push(holders[index]);
                }
                index = index + 1;
            }

            return top5Holders;
          } catch (error) {
              console.log(error);
              return [];
          }
      },
      getAllRewards: async function(wallet) {
            Contract.setProvider("https://bsc-dataseed.binance.org");
            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';

            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
            let decimals = await contract.methods.decimals().call();
            let info = await contract.methods.getAccountDividendsInfo(wallet).call();
            // console.log(info);
            return info[4] / Math.pow(10, decimals)
        },
      getFatCakePrice: async function() {
          let bnbPrice = await this.getBNBPrice();

          Contract.setProvider("https://bsc-dataseed.binance.org");
          let contract = new Contract(JSON.parse(this.lpPairAbi), this.fatCakeLPAddress);
          let reserves = await contract.methods.getReserves().call();
          let fatcake = reserves['0'] / Math.pow(10, 18);
          let wbnb = reserves['1'] / Math.pow(10, 18);

          let fatcakePriceBNB = wbnb / fatcake;
          return fatcakePriceBNB * bnbPrice;
      },
      getCakePrice: async function() {
          Contract.setProvider("https://bsc-dataseed.binance.org");
          let contract = new Contract(JSON.parse(this.lpPairAbi), "0x804678fa97d91B974ec2af3c843270886528a9E6");
          let reserves = await contract.methods.getReserves().call();
          let wbnb = reserves['0'] / Math.pow(10, 18);
          let busd = reserves['1'] / Math.pow(10, 18);
          return busd / wbnb;
      },
      getBNBPrice: async function() {
          Contract.setProvider("https://bsc-dataseed.binance.org");
          let contract = new Contract(JSON.parse(this.lpPairAbi), this.bnbLPAddress);
          let reserves = await contract.methods.getReserves().call();
          let wbnb = reserves['0'] / Math.pow(10, 18);
          let busd = reserves['1'] / Math.pow(10, 18);
          return busd / wbnb;
      },
  },
};
</script>

